// client/src/components/CreateEmployee.js
import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function CreateEmployee() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    mobile: '',
    designation: '',
    gender: '',
    courses: [],
    image: null,
  });
  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;

    if(type === 'checkbox') {
      if(checked){
        setFormData(prevState => ({
          ...prevState,
          courses: [...prevState.courses, value]
        }));
      } else {
        setFormData(prevState => ({
          ...prevState,
          courses: prevState.courses.filter(course => course !== value)
        }));
      }
    } else if(type === 'radio') {
      setFormData(prevState => ({
        ...prevState,
        [name]: value
      }));
    } else {
      setFormData(prevState => ({
        ...prevState,
        [name]: value
      }));
    }
  };

  const handleFileChange = (e) => {
    setFormData(prevState => ({
      ...prevState,
      image: e.target.files[0]
    }));
  };

  const validateEmail = (email) => {
    // Simple email regex
    const re = /\S+@\S+\.\S+/;
    return re.test(email);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate email
    if (!validateEmail(formData.email)) {
      alert('Please enter a valid email');
      return;
    }

    // Validate image type
    if(formData.image && !['image/jpeg', 'image/jpg', 'image/png'].includes(formData.image.type)){
      alert('Only JPG and PNG images are allowed');
      return;
    }

    const data = new FormData();
    data.append('name', formData.name);
    data.append('email', formData.email);
    data.append('mobile', formData.mobile);
    data.append('designation', formData.designation);
    data.append('gender', formData.gender);
    formData.courses.forEach(course => data.append('courses', course));
    if(formData.image) data.append('image', formData.image);

    try {
      await axios.post('/api/employees/create', data, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      alert('Employee created successfully');
      navigate('/employee-list');
    } catch (error) {
      console.error(error);
      alert('Error creating employee');
    }
  };

  return (
    <div style={styles.container}>
      <h2>Create Employee Account</h2>
      <form onSubmit={handleSubmit} style={styles.form}>
        <input 
          type="text" 
          name="name" 
          placeholder="Name" 
          value={formData.name} 
          onChange={handleChange} 
          required 
          style={styles.input}
        />
        <input 
          type="email" 
          name="email" 
          placeholder="Email" 
          value={formData.email} 
          onChange={handleChange} 
          required 
          style={styles.input}
        />
        <input 
          type="text" 
          name="mobile" 
          placeholder="Mobile Number" 
          value={formData.mobile} 
          onChange={handleChange} 
          style={styles.input}
        />
        <select 
          name="designation" 
          value={formData.designation} 
          onChange={handleChange} 
          required 
          style={styles.input}
        >
          <option value="">Select Designation</option>
          <option value="Developer">Developer</option>
          <option value="Manager">Manager</option>
          <option value="Designer">Designer</option>
          <option value="QA">QA</option>
        </select>
        <div style={styles.radioContainer}>
          <label>
            <input 
              type="radio" 
              name="gender" 
              value="Male" 
              checked={formData.gender === 'Male'} 
              onChange={handleChange} 
              required 
            /> Male
          </label>
          <label>
            <input 
              type="radio" 
              name="gender" 
              value="Female" 
              checked={formData.gender === 'Female'} 
              onChange={handleChange} 
              required 
            /> Female
          </label>
        </div>
        <div style={styles.checkboxContainer}>
          <label>
            <input 
              type="checkbox" 
              name="courses" 
              value="React" 
              onChange={handleChange} 
            /> React
          </label>
          <label>
            <input 
              type="checkbox" 
              name="courses" 
              value="Node.js" 
              onChange={handleChange} 
            /> Node.js
          </label>
          <label>
            <input 
              type="checkbox" 
              name="courses" 
              value="MongoDB" 
              onChange={handleChange} 
            /> MongoDB
          </label>
        </div>
        <input 
          type="file" 
          name="image" 
          accept="image/jpeg, image/png" 
          onChange={handleFileChange} 
          style={styles.input}
        />
        <button type="submit" style={styles.button}>Create Account</button>
      </form>
    </div>
  );
}

const styles = {
  container: { 
    display: 'flex', 
    flexDirection: 'column', 
    alignItems: 'center', 
    marginTop: '50px' 
  },
  form: { 
    display: 'flex', 
    flexDirection: 'column', 
    width: '400px' 
  },
  input: { 
    padding: '10px', 
    margin: '10px 0', 
    fontSize: '16px' 
  },
  radioContainer: { 
    display: 'flex', 
    justifyContent: 'space-around', 
    margin: '10px 0' 
  },
  checkboxContainer: { 
    display: 'flex', 
    justifyContent: 'space-around', 
    margin: '10px 0' 
  },
  button: { 
    padding: '10px', 
    fontSize: '16px', 
    cursor: 'pointer', 
    marginTop: '20px' 
  }
};

export default CreateEmployee;
