// client/src/components/EmployeeList.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';

function EmployeeList() {
  const [employees, setEmployees] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    fetchEmployees();
  }, []);

  const fetchEmployees = async () => {
    try {
      const response = await axios.get('/api/employees/list');
      setEmployees(response.data);
    } catch (error) {
      console.error(error);
      alert('Error fetching employees');
    }
  };

  const handleDelete = async (id) => {
    if(window.confirm('Are you sure you want to delete this employee?')){
      try {
        await axios.delete(`/api/employees/delete/${id}`);
        setEmployees(employees.filter(emp => emp._id !== id));
      } catch (error) {
        console.error(error);
        alert('Error deleting employee');
      }
    }
  };

  const handleSearch = (e) => {
    setSearchTerm(e.target.value);
  };

  const filteredEmployees = employees.filter(emp => 
    emp.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    emp.email.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div style={styles.container}>
      <h2>Employee List</h2>
      <input 
        type="text" 
        placeholder="Search by name or email" 
        value={searchTerm} 
        onChange={handleSearch} 
        style={styles.search}
      />
      <table style={styles.table}>
        <thead>
          <tr>
            <th>Unique ID</th>
            <th>Image</th>
            <th>Name</th>
            <th>Email</th>
            <th>Mobile</th>
            <th>Designation</th>
            <th>Gender</th>
            <th>Courses</th>
            <th>Created At</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {filteredEmployees.map(emp => (
            <tr key={emp._id}>
              <td>{emp._id}</td>
              <td>
                {emp.image ? (
                  <img 
                    src={`/uploads/${emp.image}`} 
                    alt={emp.name} 
                    style={styles.image} 
                  />
                ) : 'N/A'}
              </td>
              <td>{emp.name}</td>
              <td>{emp.email}</td>
              <td>{emp.mobile}</td>
              <td>{emp.designation}</td>
              <td>{emp.gender}</td>
              <td>{emp.courses.join(', ')}</td>
              <td>{new Date(emp.createdAt).toLocaleDateString()}</td>
              <td>
                {/* Implement Edit functionality as needed */}
                <button onClick={() => alert('Edit functionality not implemented')} style={styles.actionButton}>Edit</button>
                <button onClick={() => handleDelete(emp._id)} style={styles.actionButton}>Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

const styles = {
  container: { 
    padding: '20px' 
  },
  search: { 
    padding: '10px', 
    width: '300px', 
    marginBottom: '20px', 
    fontSize: '16px' 
  },
  table: { 
    width: '100%', 
    borderCollapse: 'collapse' 
  },
  image: { 
    width: '50px', 
    height: '50px', 
    objectFit: 'cover', 
    borderRadius: '50%' 
  },
  actionButton: { 
    margin: '0 5px', 
    padding: '5px 10px', 
    cursor: 'pointer' 
  }
};

export default EmployeeList;
